import React, { useContext, useEffect, useState } from "react";
import AboutManagem from "../AboutAll/About company/AboutManagem.jsx";
import Carousel from "../MainHome/Carousel/Carousel.jsx";
import HomeSustainability from "../MainHome/HomeSustainability/HomeSustainability.jsx";
import News from "../News/News.jsx";
import { homePageContext } from "../Context/homePageContext.js";
import axios from "axios";
import { baseUrl } from "../../BaseUrl/baseUrl.js";
import Relations from "../MainHome/Relations/Relations.jsx";


export default function Home({ loading, setloading }) {
  let { x } = useContext(homePageContext)
  const [data, setdata] = useState()

  let logo, umRus, umshakieb, global, sepro, als, ang, surveying, srk, srkVisit, emra, sustainability, isk, sus1, contact, news, sus2, career;//photos
  if (x != null || x != undefined) {
    logo = x[3]?.secure_url;
    umRus = x[15]?.secure_url;
    umshakieb = x[14]?.secure_url;
    srk = x[18]?.secure_url;
    surveying = x[19]?.secure_url;
    ang = x[2]?.secure_url;
    als = x[1]?.secure_url;
    sepro = x[16]?.secure_url;
    global = x[9]?.secure_url;
    emra = x[11]?.secure_url;
    srkVisit = x[17]?.secure_url;
    sustainability = x[8]?.secure_url;
    isk = x[12]?.secure_url;
  }
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `homepage/homepagedata`)
          .then(data => {
            if (data !== null || data !== undefined) {
              setdata(data?.data?.allData[0])
            }
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  return <>
    <div className="container-fluid p-0">
      {data?.header && < Carousel header={data?.header} />}
      {logo && umRus && umshakieb && data?.ourProjectsText && data?.aboutUsText && <AboutManagem logo={logo} aboutUsText={data?.aboutUsText} projectText={data?.ourProjectsText} umrus={umRus} umshakieb={umshakieb} />}
      {sustainability && <HomeSustainability sustainability={sustainability} />}
      {emra && isk && srkVisit && data?.newsText && data?.newsDetails && < News emra={emra} srkVisit={srkVisit} newsDetails={data?.newsDetails} newsText={data?.newsText} />}
      {global && sepro && als && ang && srk && isk && data?.consultantText && <Relations global={global} sepro={sepro} als={als} ang={ang} surveying={surveying} srk={srk} isk={isk} consultant={data?.consultantText} />}
    </div>
  </>

}


