import React, { useContext } from 'react'
import Slider from '../Carousel/Carousel';
import { projectPageContext } from '../Context/projectPageContext'
export default function Mapping() {
  let { Data, imgs } = useContext(projectPageContext)
  let ddPhoto;
  if (imgs !== null && imgs !== undefined) {
    ddPhoto = [{ image: imgs[17]?.secure_url }, { image: imgs[18]?.secure_url }, { image: imgs[19]?.secure_url }]
  }
  return <>
    <div className="container my-5">
      <div className="row justify-content-center text-center">
        <h5 className='main-color text-start'>{Data?.DiamondDrilling}</h5>
        <div className="col-md-12 ">
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.DiamondDrillingDetails?.split("...")[0]}</p>
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.DiamondDrillingDetails?.split("...")[1]}</p>
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.DiamondDrillingDetails?.split("...")[2]}</p>
        </div>
        <div className="col-md-12">
          <Slider imgs={ddPhoto} height={'1000px'} />
        </div>
      </div>
    </div>
  </>
}
