import React from 'react';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
export default function Carousell({ imgs, height }) {
    const options = {
        items: 1,
        loop: true,
        autoplay: true,
        autoplayTimeout: 4000,
        animateout: 'slideoutup',
        dots: false,
        center: true,
        responsive: {
            1100: { items: 2 },
            724: { items: 1 },
            500: { items: 1 },
            370: { items: 1, innerwidth: "100%", outerwidth: "100%" }
        }
    }
    return <>
        <div className="container" style={{ marginLeft: '0' }}>
            <div className="row d-flex justify-content-center">
                {imgs ? <OwlCarousel className='owl-theme text-center mt-5'  {...options} >
                    {imgs.map((ele, index) => {
                        return <div className="col text-center">
                            <a href ={ele.url} target='_blank' style={{ textDecoration: 'none' }}>
                                <div class='text-center item'>
                                    <div>
                                    <img key={index}  height={'100px'}  src={ele.image} alt="" />
                                    </div>
                                    <p className='main-color fw-bold' style={{ fontSize: '15px' }}>{ele.title}</p>
                                </div></a>
                        </div>
                    })}
                </OwlCarousel > : ""
                }
            </div>
        </div>
    </>
}
