import React, { useContext } from 'react'
import { sustainabilityPageContext } from '../../Context/sustainabilityPageContext'

export default function Social() {
  let { data } = useContext(sustainabilityPageContext)
  return <>
    <div className="container my-5">
      <h5 className='mb-4 fs-4' style={{ color: '#E66626' }}>{data?.SocialAndEconomicDevelopment}</h5>
      <div className="row">
        <h5 className='fs-4' style={{ color: '#E66626' }}>{data?.OurSocialConduct}</h5>
        <p className='fs-5' style={{ color: '#686868' }}>{data?.Ourbusinesscanplayanimportant}</p>
        <ul>
          <li className='fw-light fs-5' style={{ color: '#686868' }}>{data?.SocialEconomicDetails.split('...')[0]}</li>
          <li className='fw-light fs-5' style={{ color: '#686868' }}>{data?.SocialEconomicDetails.split('...')[1]}</li>
          <li className='fw-light fs-5' style={{ color: '#686868' }}>{data?.SocialEconomicDetails.split('...')[2]}</li></ul>
      </div>
    </div>
    <div className='container-fluid' style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
      <div className="container">
        <div className="row py-5 mb-5 ">
          <h5 className='fs-4' style={{ color: '#E66626' }}>{data?.Governance}</h5>
          <ul>
            <li className='fw-light fs-5' style={{ color: '#686868' }}>{data?.GovernanceDetails.split('...')[0]}</li>
            <li className='fw-light fs-5' style={{ color: '#686868' }}>{data?.GovernanceDetails.split('...')[1]}</li></ul>
        </div>
      </div>
    </div>
  </>
}
