import { createContext, useEffect, useState } from "react";
import { baseUrl } from "../../BaseUrl/baseUrl";
import axios from "axios";
export let homePageContext = createContext(0)
export function HomePageContextProvider(props) {
    const [x, setx] = useState()
    const [homeData, setHomeData] = useState()

    useEffect(() => {
        function getImgs() {
            setTimeout(() => {
                axios.get(baseUrl + `homepage/homepageimg`)
                    .then(data => {
                        setx(data?.data?.allPhotos[0]?.images);
                    }).catch(err => console.log(err))
            }, 500);
        }
        getImgs()
    }, [])
    useEffect(() => {
        function getImgs() {
            setTimeout(() => {
                axios.get(baseUrl + `homepage/homepagedata`)
                    .then(res => {
                        console.log(res.data.allData[0]);
                        setHomeData(res.data.allData[0]);
                    }).catch(err => console.log(err))
            }, 500);
        }
        getImgs()
    }, [])
    return <homePageContext.Provider value={{ x, setx,homeData }}>{props.children}</homePageContext.Provider>
}