import React, { useEffect, useState } from 'react'
import { baseUrl } from '../../BaseUrl/baseUrl'
import axios from 'axios'
import { useParams } from 'react-router-dom'
export default function QRCode() {
    const [employee, setemployee] = useState()
    const {id} = useParams()
    useEffect(() => {
        setTimeout(() => {
            axios.get(baseUrl + `employees/${id}`)
                .then(data => {
                    if (data !== null || data !== undefined) {
                        setemployee(data?.data?.employee)
                    }
                }).catch(err => console.log(err))
        }, 500);
    }, [])
    return (
        
            <div className="container my-5 text-muted">
                <div className="row justify-content-between align-items-center">
                    <div className="col-md-10 col-sm-10 col-xs-10">
                        <h4 className='main-color'>ID: <span className='text-secondary'>{employee?.employeeId}</span></h4>
                        <h4 className='main-color'>Name: <span className='text-secondary'>{employee?.name}</span></h4>
                        <h4 className='main-color'>COMPANY: <span className='text-secondary'>{employee?.companyName}</span></h4>
                        <h4 className='main-color'>TITLE: <span className='text-secondary'>{employee?.title}</span></h4>
                        <h4 className='main-color'>BLOOD: <span className='text-secondary'>{employee?.blood}</span></h4>
                        <h4 className='main-color'>PHONE: <span className='text-secondary'>{employee?.phone}</span></h4>
                        <h4 className='main-color'>EMAIL: <span className='text-secondary'>{employee?.email}</span></h4>
                        <h4 className='main-color'>VALIDATE THRU: <span className='text-secondary' typeof='date'>{employee?.validatethru}</span></h4>
                        
                    </div>
                    <div className="col-md-2 col-sm-2 col-xs-2">
                        <img className='w-100' src={employee?.image?.secure_url} alt="" />
                    </div>
                </div>
            </div>
    )
}



