import React, { useContext } from 'react'
import { projectPageContext } from '../Context/projectPageContext'
import Slider from '../Carousel/Carousel';

export default function Mapping() {
  let { Data, imgs } = useContext(projectPageContext)
  let rcPhoto;
  if (imgs !== null && imgs !== undefined) {
    rcPhoto = [{ image: imgs[14]?.secure_url }, { image: imgs[15]?.secure_url }, { image: imgs[16]?.secure_url }]
  }

  return <>
    <div className="container my-5">
      <div className="row justify-content-center text-center">
        <h5 className='main-color text-start'>{Data?.RCDrilling}</h5>
        <div className="col-md-12 ">
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.RCDrillingDetails?.split("...")[0]}</p>
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.RCDrillingDetails?.split("...")[1]}</p>
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.RCDrillingDetails?.split("...")[2]}</p>
        </div>
        <div className="col-md-12">
          <Slider imgs={rcPhoto} height={'1000px'}/>
        </div>
      </div>
    </div>
  </>
}
