import React, { useContext } from 'react'
import img from '../../../images/safety/16.bmp'
import styles from './Health.css'
import img2 from '../../../images/safety/Capture.PNG'
import img1 from '../../../images/safety/Capture2.PNG'
import { sustainabilityPageContext } from '../../Context/sustainabilityPageContext'
import { homePageContext } from '../../Context/homePageContext'

export default function HealthandSafety() {
  // let { x } = useContext(homePageContext)
  // let susImg, healthImg;
  // if (x !== null && x !== undefined) {
  //   susImg = x[12]?.secure_url;
  //   healthImg = x[18]?.secure_url;
  // }
  let { data } = useContext(sustainabilityPageContext)
  return <>
  {<>
    <div className="container">
      <div className="row justify-content-center my-3">
        <h3 style={{ color: '#E66626', textAlign: 'center' }}>{data?.SafetyIsOurPriority}</h3>
        <div className="col-md-10">
          <p className='fs-5' style={{ color: '#686868', textAlign: 'center' }}>{data?.SafetyIsOurPriorityDetails}</p>
        </div>
      </div></div>
    <div className="container-fluid my-3 p-2" style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
      <div className="container">
        <div className="row">
          <div className="col-md-9">
            <h5 className='fs-4' style={{ color: '#E66626' }}>{data?.OurHSEPolicy}</h5>
            <h6 className='fw-normal p-3' style={{ color: '#686868' }}> <ul>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[0]}</li>
              <li className='fs-5'> {data?.OurHSEPolicyDetails.split('...')[1]}</li>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[2]}</li>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[3]}</li>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[4]}</li>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[5]}</li>
              <li className='fs-5'>{data?.OurHSEPolicyDetails.split('...')[6]}</li>
            </ul></h6>
            <div className="container-fluid my-4">
              <h5 className='fs-4' style={{ color: '#E66626' }}>{data?.EmployeesHealthandSafety}</h5>
              <div className="container">
                <h6 className='fs-4' style={{ color: '#E66626' }}>{data?.EmployeesPriority}</h6>
                <h6 className='fw-normal p-3 fs-5' style={{ color: '#686868' }}> <ul style={{ color: '#686868' }}><li>{data?.employeesDetails.split('...')[0]}</li>
                  <li>{data?.employeesDetails.split('...')[1]}</li>
                  <li>{data?.employeesDetails.split('...')[2]}</li>
                  <li>{data?.employeesDetails.split('...')[3]}</li>
                  <li>{data?.employeesDetails.split('...')[4]}</li>
                  <li>{data?.employeesDetails.split('...')[5]}</li>
                </ul></h6>
              </div>
            </div>
          </div>
          <div className="col-md-3">
            <img src='' className='w-100 mb-3' alt="" />
            <img src='' className='w-100' alt="" />
          </div>
        </div>
      </div>
    </div>
    <div className="container my-3">
      <div className="row">
        <h5 className='fs-4' style={{ color: '#E66626' }}>{data?.EnvironmentalProtection}</h5>
        <p className='fs-5' style={{ color: '#E66626' }}>{data?.EnvPriority}</p>
        <h5 className='fw-normal p-3' style={{ color: '#686868' }}> <ul style={{ color: '#686868' }}>
          <li>{data?.EnvironmentalDetails.split('...')[0]}</li>
          <li>{data?.EnvironmentalDetails.split('...')[1]}</li>
          <li>{data?.EnvironmentalDetails.split('...')[2]}</li>
          <li>{data?.EnvironmentalDetails.split('...')[3]}</li>
          <li> {data?.EnvironmentalDetails.split('...')[4]}</li>
        </ul></h5>
        <p className='fw-normal p-3 fs-5' style={{ color: '#686868' }}> This is the foundation of our commitment to conducting our business in a responsible manner with the health and safety of people and the environment as our top considerations. </p>
      </div>
    </div>
    <div className="container pt-5" >
      <h5 className='mb-4 fs-4' style={{ color: '#E66626' }}>{data?.EnvironmentalResponsibility}</h5>
      <div className="row justify-content-center mb-5">

        <h6 className='fw-normal fs-4' style={{ color: '#686868' }}>{data?.EnvironmentalStewardship}</h6>
        <div className="col-md-3 text-center"><img className='w-100' src='' alt="" /></div>
        <div className="col-md-9">
          <ul style={{ color: '#727272' }}>
            <li className='fs-5 fw-normal'>{data?.EnvironmentalResponsibilityDetails.split('...')[0]}</li>
            <li className='fs-5 fw-normal'>{data?.EnvironmentalResponsibilityDetails.split('...')[1]}</li>
            <li className='fs-5 fw-normal'>{data?.EnvironmentalResponsibilityDetails.split('...')[2]}</li>
          </ul>
        </div>
      </div>
    </div></>}
  </>
}
