import { createContext, useEffect, useState } from "react";
import { baseUrl } from "../../BaseUrl/baseUrl";
import axios from "axios";
export let aboutUsPageContext = createContext(0)
export function AboutUsPageContextProvider(props) {
    const [aboutUs, setaboutUs] = useState()
    useEffect(() => {
        function getImgs() {
            setTimeout(() => {
                axios.get(baseUrl + `aboutpage/aboutuspagedata`)
                    .then(data => {
                        setaboutUs(data?.data?.allData[0]);
                    }).catch(err => console.log(err))
            }, 500);
        }
        getImgs()
    }, [])
    return <aboutUsPageContext.Provider value={{ aboutUs }}>{props.children}</aboutUsPageContext.Provider>
}