import React, { useContext } from 'react'
import { projectPageContext } from '../Context/projectPageContext'
import Slider from '../Carousel/Carousel';
export default function Mapping() {
  let { Data, imgs } = useContext(projectPageContext)
  let trenchPhoto;
  if (imgs !== null && imgs !== undefined) {
    trenchPhoto = [{ image: imgs[12]?.secure_url }, { image: imgs[13]?.secure_url }]
  }

  return <>
    <div className="container my-5">
      <div className="row justify-content-center text-center">
        <h5 className='main-color text-start'>{Data?.Trench}</h5>
        <div className="col-md-12 ">
          <h6 className='fs-5 main-color'>{Data?.TrenchDetails?.split('...')[0]}</h6>
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.TrenchDetails?.split('...')[1]}</p>
          <ol className='text-start mt-3 secondary-color fs-5'><li>
            <p>{Data?.TrenchDetails?.split('...')[2]}</p>
          </li>
            <li>
              <p >{Data?.TrenchDetails?.split('...')[2]}</p>
            </li>
            <li>
              <p >{Data?.TrenchDetails?.split('...')[3]}</p>
            </li>

            <li>
              <p >{Data?.TrenchDetails?.split('...')[4]}</p>
            </li>
            <li>
              <p >{Data?.TrenchDetails?.split('...')[5]}</p>
            </li>
            <li>
              <p >{Data?.TrenchDetails?.split('...')[6]}</p>
            </li>
          </ol>
          <p className='text-center mt-3 main-color fs-5'>{Data?.TrenchDetails?.split('...')[7]}</p>
        </div>
        <div className="col-md-12">
          <Slider imgs={trenchPhoto} height={'1000px'} />
        </div>
      </div>
    </div>
  </>
}
