import React, { useContext } from 'react';
import { homePageContext } from '../../Context/homePageContext';
export default function Carousel(props) {
    let { x } = useContext(homePageContext)
    let bg;
    if (x != null || x != undefined) {
        bg = x[0]?.secure_url;
    }
    return <>

        {bg && <div className='img p-0' style={{ backgroundImage: `url(${bg})`, position: 'relative' }}>
            <div className='d-flex justify-content-center align-items-center bgimage '>
                <h1 className={`text-white position-absolute typing fw-light`}>{props.header}</h1>
            </div>
        </div>}
    </>
};
