import React, { useContext, useEffect, useState } from 'react'
import { NavLink, Outlet } from 'react-router-dom'
import { baseUrl } from '../../../BaseUrl/baseUrl'
import axios from 'axios'
import { aboutUsPageContext } from '../../Context/aboutUsPageDataContext'
export default function About() {
  const [img, setimg] = useState()
  const { aboutUs } = useContext(aboutUsPageContext)
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `aboutpage/aboutuspageimg`)
          .then(data => {
            if (data !== null || data !== undefined) {
              setimg(data)
            }
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  let bg;
  if (img !== null && img !== undefined) {
    bg = img?.data?.allPhotos[0]?.images[0]?.secure_url
  }
  return <>
    {bg && <>
      <header className='aboutImg'>
        <div className='aboutimg' style={{ backgroundImage: `url( ${bg} )` }}>
          <div style={{ backgroundColor: 'rgba(0,0,0,0.1)', width: '100%', height: '100%' }}></div>
        </div>
      </header>
      <div className={`containerfluid container-fluid p-0 overflow-hidden my-5`}>
        <div className="row m-0">
          <h3 className='underlineh3' style={{ color: '#686868', position: 'relative' }}>{aboutUs?.aboutnavtabs}</h3>
        </div>
        <div className="row mt-4 d-flex justify-content-center align-items-center text-center py-4 paragraphcolor">
          <div className="col-md-4"><NavLink style={{ cursor: 'pointer', textDecoration: 'none', textDecorationStyle: 'none', color: '#E66626' }} to='corporate'><h6>CORPORATE</h6></NavLink></div>
          <div className="col-md-4"> <NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} to='management'><h6>ChairMan Statement</h6></NavLink></div>
          <div className="col-md-4" ><NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} to='strategicpartners'><h6>Strategic Partners</h6></NavLink></div>
        </div>
        <Outlet />
      </div></>}
  </>
}
