import React, { useContext } from "react";
import contactstyles from "./Contact.css";
import { homePageContext } from "../Context/homePageContext";
import emailjs from "emailjs-com"
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
export default function Contact() {
  const { x } = useContext(homePageContext)
  let contact;
  if (x !== null && x !== undefined) {
    contact = x[23]?.secure_url;
  }
  const notify = () => toast("Message sent successfully");
  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('service_m6s84yc', 'template_z2gg0cn', e.target, 'A2iBfObQoSdqavwYf')
      .then(res => console.log(res)).catch(err => console.log(err))
  }

  return (
    <>
      <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-inner position-relative " style={{ height: "63vh" }} >
          <div className="carousel-item active">
            <div className="img" style={{ backgroundImage: `url(${contact})` }}><div style={{ backgroundColor: 'rgba(0,0,0,0.1)', width: '100%', height: '100%' }}></div></div>
            {/* <img src={img} className="d-block w-100 contactimg" alt="..." /> */}
            <div className="overlay w-100 h-100 position-absolute top-0 start-0 left-0 bottom-0" style={{ backgroundSize: "cover", backgroundPosition: "center", backgroundColor: "rgba(0,0,0,0.1)" }}></div>
          </div>
          <div className="d-flex justify-content-center align-items-center">
            <h3 className={`text-white position-absolute text fw-light ${contactstyles.abouttext}`} style={{ paddingTop: "30%" }} > </h3>
          </div>
        </div>
      </div>
      <div className="container my-5">
        <div className="row">
          <iframe height="500px" width="500px" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d121793.57025587748!2d31.29107977958291!3d30.10807545902944!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x1458160afe43caab%3A0x2b62b30e0ec95b86!2s197%20El-Orouba%2C%20Sheraton%20Al%20Matar%2C%20El%20Nozha%2C%20Cairo%20Governorate%204471011%2C%20Egypt!5e0!3m2!1sen!2str!4v1660500264393!5m2!1sen!2str"
            frameborder="0"></iframe>
        </div>
        <div className="row mt-5">
          <div className="col-md-4">
            <div style={{ color: "#E66626" }}>
              <span>
                <i class="fa-solid fa-location-dot me-3"></i>
              </span>
              Address: 197 Al-Oroba  Nozha  Egypt.
            </div>
            <div className="my-2" style={{ color: "#E66626" }}>
              <span>
                <i class="fa-solid fa-phone  me-3"></i>
              </span>
              Phone: +202 22 682 188
            </div>
            <div style={{ color: "#E66626" }}>
              <span>
                <i class="fa-solid fa-envelope me-3"></i>
              </span>
              E-mail: info@nubianmining.com
            </div>
          </div>
          <div className="col-md-8">
            <h4 style={{ color: "#E66626" }}>GET IN TOUCH WITH US</h4>
            <form className="form-control" onSubmit={sendEmail} target="_blank" method="POST">
              <div className="container text-center">
                <div className="row justify-content-center gy-4">
                  <div className="col-md-6">
                    <input className="form-control" name="name" placeholder="Name" type="text" />
                  </div>
                  <div className="col-md-6">
                    <input className="form-control" name="email" placeholder="E-mail" type="email" />
                  </div>
                  <div className="col-md-6">
                    <input className="form-control" name="phone" placeholder="Phone Number" type="phone" />
                  </div>
                  <div className="col-md-6">
                    <input className="form-control" name="subject" placeholder="Subject" type="text" />
                  </div>
                  <div className="col-md-12">
                    <textarea name="message" placeholder="Message" className="form-control" cols="30" rows="10" ></textarea>
                  </div>
                  <div className="col-md-4">
                    <button onClick={notify} className="btn mainbtn">Send Message</button>
                    <ToastContainer position="top-right"
                      autoClose={5000}
                      hideProgressBar={false}
                      newestOnTop={false}
                      closeOnClick
                      rtl={false}
                      pauseOnFocusLoss
                      draggable
                      pauseOnHover
                      theme="light"
                    />
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div >
    </>
  );
}
