import Carousel from 'react-bootstrap/Carousel';
function Slider({ imgs,height }) {
    let x = imgs;

    return (
        x ?
            <Carousel data-bs-theme="dark">
                {x.map((ele) => {
                    return <Carousel.Item>
                        <img className="d-block w-100" height={height} src={ele.image} alt="First slide" />
                        <Carousel.Caption>
                            {ele.title ? <h5>{ele.title}</h5> : ''}
                            {ele.words ? <p>{ele.words}</p> : ''}
                        </Carousel.Caption>
                    </Carousel.Item>
                })}
            </Carousel> : ''
    )
}
export default Slider;




