import React, { useEffect, useState } from "react";
import { RouterProvider, createHashRouter } from "react-router-dom";
import LayOut from "./Components/LayOut/LayOut.jsx";
import Home from "./Components/Home/Home.jsx";
import Sustainability from "./Components/MainSustainability/Sustainability/Sustainability.jsx";
import Ourprojects from "./Components/Ourprojects/Ourprojects.jsx";
import Corporate from "./Components/AboutAll/Corporate/Corporate.jsx";
import Management from "./Components/AboutAll/Management/Management.jsx";
import Contact from "./Components/Contact/Contact.jsx";
import Footer from "./Components/Footer/Footer.jsx";
import About from "./Components/AboutAll/About/About.jsx";
import InvalidRouting from "./Components/InvalidRouting/InvalidRouting.jsx";
import UmRuus from "./Components/UmRuus/UmRuus.jsx";
import UmShakaieb from "./Components/UmShakaieb/UmShakaieb.jsx";
import HealthandSafety from "./Components/MainSustainability/HealthAndSafety/HealthAndSafety.jsx";
import Social from "./Components/MainSustainability/Social/Social.jsx";
import OurPeople from "./Components/MainSustainability/OurPeople/OurPeople.jsx";
import Career from "./Components/Career/Career.jsx";
import StrategicParteners from "./Components/AboutAll/StrategicPartners/StrategicPartners.jsx";
import EMRAVISIT from "./Components/Main News/NewsDetails/NewsDetails.jsx";
import JobApply from "./Components/JobApply/JobApply.jsx";
import { HomePageContextProvider } from "./Components/Context/homePageContext.js";
import { AboutUsPageContextProvider } from "./Components/Context/aboutUsPageDataContext.js";
import { ProjectPageContextProvider } from "./Components/Context/projectPageContext.js";
import { SustainabilityPageContextProvider } from "./Components/Context/sustainabilityPageContext.js";
import { NewsPageContextProvider } from "./Components/Context/newsPageContext.js";
import QRCode from "./Components/QRCode/QRCode.jsx";
import InsideNews from "./Components/Main News/insideNews.jsx";
import Mapping from "./Components/UmRuus/Mapping.jsx";
import Rc from "./Components/UmRuus/Rc.jsx";
import DD from "./Components/UmRuus/DD.jsx";
import Trench from "./Components/UmRuus/Trench.jsx";
import Slider from "./Components/Carousel/Carousel.jsx"
import Carousell from "./Components/Slider/Carousell.jsx";
import NewsDetails from "./Components/Main News/NewsDetails/NewsDetails.jsx";
import AllNews from "./Components/AllNews/AllNews.jsx";
export default function App() {
  const [loading, setloading] = useState(false)
  let routers = createHashRouter([
    {
      path: "",
      element: <LayOut />,
      children: [
        { path: "", element: <Home /> },
        { path: "home", element: <Home loading={loading} setloading={setloading} /> },
        { path: "qrcode/:id", element: <QRCode /> },
        {
          path: "sustainability",
          element: <Sustainability />,
          children: [
            { path: "", element: <OurPeople /> },
            { path: "ourpeople", element: <OurPeople /> },
            { path: "healthandsafety", element: <HealthandSafety /> },
            { path: "social", element: <Social /> },
          ],
        },
        {
          path: "Our-Projects",
          element: <Ourprojects />,
          children: [
            {
              path: "", element: <UmRuus />, children: [
                { path: "", element: <Mapping /> },

              ]
            },
            {
              path: "umruus", element: <UmRuus />, children: [
                { path: "mapping", element: <Mapping /> },
                { path: "rc", element: <Rc /> },
                { path: "dd", element: <DD /> },
                { path: "trench", element: <Trench /> }
              ]
            },
            { path: "umshakaieb", element: <UmShakaieb /> },
          ],
        },
        {
          path: "About-Us",
          element: <About />,
          children: [
            { path: "", element: <Corporate /> },
            { path: "corporate", element: <Corporate /> },
            { path: "management", element: <Management /> },
            { path: "strategicpartners", element: <StrategicParteners /> },
          ],
        },
        { path: "career", element: <Career /> },
        {
          path: "news",
          element: < InsideNews />,
          children: [
            { path: "", element: <EMRAVISIT /> },
            { path: "/news/:id", element: <NewsDetails /> },
          ]
        },
        { path: "Contact-Us", element: <Contact /> },
        { path: "allnews", element: <AllNews /> },
        { path: "footer", element: <Footer /> },
        { path: "jobapply", element: <JobApply /> },
        { path: "slider", element: <Slider /> },
        { path: "carousell", element: <Carousell /> },
        { path: "*", element: <InvalidRouting /> },
      ],
    },
  ]);

  return <HomePageContextProvider>
    <AboutUsPageContextProvider>
      <ProjectPageContextProvider>
        <SustainabilityPageContextProvider>
          <NewsPageContextProvider>
            <RouterProvider router={routers}></RouterProvider>
          </NewsPageContextProvider>
        </SustainabilityPageContextProvider>
      </ProjectPageContextProvider>
    </AboutUsPageContextProvider>
  </HomePageContextProvider>

}
