import { createContext, useEffect, useState } from "react";
import { baseUrl } from "../../BaseUrl/baseUrl";
import axios from "axios";
export let projectPageContext = createContext(0)
export function ProjectPageContextProvider(props) {
  const [Data, setData] = useState()
  const [imgs, setImgs] = useState()
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `projectpage/projectpagedata`)
          .then(res => {
            setData(res?.data?.allData[0]);
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  useEffect(() => {
    function getImgs() {
      setTimeout(() => {
        axios.get(baseUrl + `projectpage/projectpageimg`)
          .then(x => {
            setImgs(x?.data?.allPhotos[0]?.images);
          }).catch(err => console.log(err))
      }, 500);
    }
    getImgs()
  }, [])
  return <projectPageContext.Provider value={{ Data, setData, imgs, setImgs }}>{props.children}</projectPageContext.Provider>
}