import React, { useContext } from 'react'
import { newsPageContext } from '../Context/newsPageContext'
import { NavLink } from 'react-router-dom';
import { homePageContext } from '../Context/homePageContext';
export default function AllNews() {
    const { newsData } = useContext(newsPageContext)
    const { x } = useContext(homePageContext)
    let y;
    if (x !== null && x !== undefined) {
        console.log(x[25]?.secure_url);
        y = x[25]?.secure_url
    }
    return <>
        <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
            <div className="carousel-inner position-relative ">
                <div className="carousel-item active">
                    <div className='img ' style={{ backgroundImage: `url(${y})`, position: 'relative' }}>
                        <div className='bgimage'>
                            <div className='d-flex justify-content-center align-items-center'>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
        <div className="container my-5">
            <div className="row justify-content-center align-items-center gy-3">
                <h3 className='secondary-color '>All News</h3>
                {newsData?.map((ele, index) => {
                    return <div className="col-md-4" key={index}>
                        <div className="card" style={{ height: '500px' }}>
                            <img className="card-img-top" src={ele?.images[0]?.secure_url} alt="Card image cap" />
                            <div className="card-body">
                                <h5 className="card-title main-color" style={{ height: '150px' }}>{ele?.title}</h5>
                                <hr />
                                <NavLink to={`/news/${ele?._id}`} className="btn mainbtn">Read More</NavLink>
                            </div>
                        </div>
                    </div>
                })}
            </div>
        </div>
    </>
}
