import React, { useContext } from 'react'
import projectstyles from './Ourprojects.module.css'
import { NavLink, Outlet } from 'react-router-dom'
import style from './Ourprojects.module.css'
import { homePageContext } from '../Context/homePageContext'
export default function Ourprojects() {
  let { x } = useContext(homePageContext)
  if (x !== null && x !== undefined)
    return <>
      <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-inner position-relative ">
          <div className="carousel-item active">
            <div className='img ' style={{ backgroundImage: `url(${x[10]?.secure_url})`, position: 'relative' }}>
              <div className='bgimage'>
                <div className='d-flex justify-content-center align-items-center'>
                </div>
              </div>
            </div>
          </div>
          <div className='d-flex justify-content-center align-items-center'>
            <h1 className={`text-white position-absolute typing fw-light ${style.project}`} style={{ paddingTop: '30%', backgroundPosition: 'bottom', backgroundSize: 'inherit' }}></h1>
          </div>
        </div>
      </div >


      
      <div className="container-fluid overflow-hidden p-0 my-5">
        <div className="row m-0">
          <h3 className={`${projectstyles.underline}`} style={{ color: '#686868', position: 'relative' }}>Our Projects</h3>
        </div>
        <div className="row mt-4 d-flex justify-content-center align-items-center text-center py-4" style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
          <div className="col-md-3"><NavLink style={{ cursor: 'pointer', textDecoration: 'none', textDecorationStyle: 'none', color: '#E66626' }} className='fs-5' to='umruus'><h6>UM EL RUS</h6></NavLink></div>
          <div className="col-md-3"> <NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} className='fs-5' to='umshakaieb'><h6>Um Shakaieb</h6></NavLink></div>
        </div>
        <Outlet />
      </div>
    </>
}


                  {/* <h1 className={`text-white position-absolute typing fw-light `} style={{ paddingTop: '30%' }}>{props.header}</h1> */}
