import React, { useContext, useEffect, useState } from "react";
import "./Footer.css";
import { NavLink } from "react-router-dom";
import { homePageContext } from "../Context/homePageContext";
import axios from "axios";
import { baseUrl } from "../../BaseUrl/baseUrl";
export default function Footer() {
  let { x } = useContext(homePageContext)
  let footerLogo,img
  if (x != null || x != undefined) {
    footerLogo = x[4]?.secure_url;
    img= x[10]?.secure_url;
  }
  const [data, setdata] = useState()
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `homepage/homepagedata`)
          .then(data => {
            if (data !== null || data !== undefined) {
              setdata(data?.data?.allData[0])
            }
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  const facebook = data?.footerFacebook
  return <>
        {data?.data?.allData[0] ?? <div
          className="container-fluid p-0 overflow-hidden sticky-sm-bottom sticky-xl-bottom sticky-xxl-bottom "
          style={{
            backgroundImage: `url(${img})`,
            position: "relative",
            backgroundRepeat: "no-repeat",
            backgroundAttachment: "fixed",
            backgroundPosition: "center",
            width: "100%",
            height: "400px",
          }}
        >
          <div className="overlay top-0 start-0 end-0 bottom-0 px-0" style={{ backgroundColor: "rgba(0,0,0,0.7)",  width: "100%", height: "auto",position: "absolute"}} >
            <div className="row d-flex justify-content-center align-items-center">
              <div className="col-md-6 d-flex justify-content-center">
                <img
                  src={footerLogo}
                  className="text-white"
                  style={{ width: "80%" }}
                  alt=""
                />
              </div>
              <div className="col-md-6 mt-5">
                <h3 className=" mb-2 text-center" style={{ color: "#E66626" }}>
                  Contact Info
                </h3>
                <ul className=" d-flex justify-content-center ">
                  <div>
                    <p className="text-white">
                      <span style={{ color: "#E66626" }}>Address :</span> {data?.footeraddress}
                    </p>
                    <p className="text-white">
                      <span
                        style={{
                          color: "#E66626",
                          transform: "translateX(-300px)",
                        }}
                      >
                        Phone :
                      </span>{" "}
                      +202 22 682 188
                    </p>
                    <p className="text-white">
                      <span style={{ color: "#E66626" }}>Email :</span>{" "}
                      {data?.footerEmail}
                    </p>
                    <div className=" d-flex justify-content-center mt-3">
                      <div style={{ cursor: "pointer" }} className='d-flex footertag justify-content-center align-items-center text-center'>
                        <NavLink to={facebook} className='socialIcon a-link' target="_blank">
                          <i className="fa-brands fa-facebook-f">

                          </i>
                        </NavLink>

                      </div>
                      <div style={{ cursor: 'pointer' }} className='d-flex footertag justify-content-center align-items-center text-center mx-3'>
                        <NavLink to={data?.footerTwitter} className='socialIcon a-link' target="_blank">
                          <i className="fa-brands fa-twitter">
                          </i>
                        </NavLink>
                      </div>
                      <div style={{ cursor: 'pointer', textDecoration: 'none' }} className='d-flex footertag justify-content-center align-items-center text-center'>
                        <NavLink to={data?.footerLinkedIn} className='socialIcon a-link link' target="_blank">
                          <i className="fa-brands fa-linkedin-in"></i></NavLink>
                      </div>
                    </div>
                  </div>
                </ul>
              </div>
            </div>
            <div
              className="border-bottom w-50 mx-auto"
              style={{ backgroundColor: "#E66626" }}
            ></div>
            <p className="text-white text-center pt-5">
               2022{" "}
              <NavLink
                to="/about"
                style={{ color: "#E66626", cursor: "pointer" }}
              >
                Nubian Mining NMC.
              </NavLink>{" "}
              All rights reserved.
            </p>
          </div>
        </div>}</>
}
