import React, { useContext } from 'react'
import { aboutUsPageContext } from '../../Context/aboutUsPageDataContext'
import { homePageContext } from '../../Context/homePageContext'
import { newsPageContext } from '../../Context/newsPageContext'
export default function StrategicParteners() {
  let { news } = useContext(newsPageContext)
  let newsImg;
  if (news !== null && news !== undefined) {
    newsImg = news;
  }
  let { x } = useContext(homePageContext)
  let logo, global, sepro, als, ang, surveying, srk, isk;//photos
  if (x != null && x != undefined) {
    logo = x[11]?.secure_url;
    srk = x[18]?.secure_url;
    surveying = x[19]?.secure_url;
    ang = x[2]?.secure_url;
    als = x[1]?.secure_url;
    sepro = x[16]?.secure_url;
    global = x[9]?.secure_url;
    isk = x[12]?.secure_url;
  }
  let { aboutUs } = useContext(aboutUsPageContext)
  var OurContractorsDetails, OurContractors, OurConsultantsDetails, OurConsultants;
  if (aboutUs !== null && aboutUs !== undefined) {
    var { OurContractorsDetails, OurContractors, OurConsultantsDetails, OurConsultants } = aboutUs
  }
  return <>
    {OurConsultants && OurConsultantsDetails && OurContractors && OurContractorsDetails && newsImg && <><div className="container mt-5" >
      <h2 className='text-center' style={{ color: '#fd7e14' }}>{OurConsultants}</h2>
      <div className="row">
        <div className="col-md-12 text-center">
          <a href="https://www.srk.com/en/" target="_blank" ><img style={{ cursor: 'pointer', width: '30%' }} object-fit="contain" src={srk} className='mb-5' alt="" /></a>
        </div>
        <div className="col-md-12">
          <p className='fs-5 fw-light secondary-color'>{OurConsultantsDetails}</p>
        </div>
      </div>
    </div>
      <div className="container" height="80vh">
        <div className="row">
          <div id="carouselExampleInterval" className="carousel slide" data-bs-ride="carousel">
            <div className="carousel-inner">
              <div className="carousel-item active" data-bs-interval="3000">
                <img src={news[0]?.secure_url} className="d-block w-100 h-50" alt="..." />
              </div>
              <div className="carousel-item" data-bs-interval="3000">
                <img src={news[1]?.secure_url} className="d-block w-100 h-50" alt="..." />
              </div>
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="prev">
              <span className="carousel-control-prev-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Next</span>
            </button>
          </div>
        </div>
      </div>
      <div className="container mt-5">
        <div className="row justify-content-center align-items-center text-center">
          <h2 style={{ color: '#fd7e14' }}>Our Partners</h2>
          <div className="col-md-4"><a href="http://surveying-systems.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={surveying} alt="" /></a>
          </div>
          <div className="col-md-4"><a href="https://arabnubia.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={ang} alt="" /></a>
          </div>
          <div className="col-md-4"><a href="https://www.alsglobal.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={als} alt="" /></a>
          </div>
          <div className="col-md-4"><a href="https://labs.seprosystems.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={sepro} alt="" /></a>
          </div>
          <div className="col-md-4"><a href="https://www.global-resource-eng.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={global} alt="" /></a>
          </div>
        </div>
      </div>
      <div className="container mt-5">
        <div className="row justify-content-center align-items-center text-center">
          <h2 style={{ color: '#fd7e14' }}>{OurContractors}</h2>
          <div className="col-md-8 my-3"><a href="https://iskcm.com/" target="_blank" ><img style={{ cursor: 'pointer', width: '40%' }} src={isk} alt="" /></a>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12">
            <p className='fs-5 fw-light secondary-color'>{OurContractorsDetails}</p>
          </div>
        </div>
      </div>
    </>}

  </>
}
