import React, { useContext } from 'react'
import { aboutUsPageContext } from '../../Context/aboutUsPageDataContext'

export default function Corporate() {
  let { aboutUs } = useContext(aboutUsPageContext)
  var OURGOALDetails, OURGOAL, MISSIONDetails, MISSION, CORPORATE, OurContractorsDetails, OurContractors, POLICYDetails, POLICY, Strategy, StrategyDetails, TARGETDetails, TARGET, INCORPORATIONDetails, INCORPORATION;
  if (aboutUs !== null && aboutUs !== undefined) {
    var { OURGOALDetails, OURGOAL, MISSIONDetails, MISSION, CORPORATE, OurContractorsDetails, OurContractors, POLICYDetails, POLICY, Strategy, StrategyDetails, TARGETDetails, TARGET, INCORPORATIONDetails, INCORPORATION } = aboutUs
    POLICYDetails = POLICYDetails.split('...')
  }
  return <>{
    OURGOALDetails && OURGOAL && MISSIONDetails && MISSION && CORPORATE && OurContractorsDetails && OurContractors && POLICYDetails && POLICY && Strategy && StrategyDetails && TARGETDetails && TARGET && INCORPORATIONDetails && INCORPORATION && <>
      <div className="container-fluid mt-5">
        <div className="row p-0 my-2 mx-auto justify-content-center align-items-baseline  py-5">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center', marginBottom: '20px' }}>{INCORPORATION}</h3>
          </div>
          <div className="col-md-9 rounded">
            <p style={{ color: '#686868', maxWidth: '1000px', textAlign: 'center' }} className='fs-5 fw-normal mx-auto'>{INCORPORATIONDetails}</p>
          </div>
        </div>
      </div>
      <div className='container-fluid' style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
        <div className="row p-0 my-2 mx-auto justify-content-center align-items-baseline py-5">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center' }}>{TARGET}</h3>
          </div>
          <div className="col-md-9" >
            <div className='mx-auto' style={{ maxWidth: '1000px', textAlign: 'center' }}>
              <p style={{ color: '#686868' }} className='fs-5 fw-light pt-3'>{TARGETDetails}</p>
            </div>
          </div></div>
      </div>
      <div className='container-fluid'>
        <div className="row p-0 my-2 mx-auto justify-content-center align-items-baseline  py-5">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center' }}>{Strategy}</h3>
          </div>
          <div className="col-md-9 rounded" >
            <div className='mx-auto' style={{ maxWidth: '1000px', textAlign: 'center' }}>
              <p style={{ color: '#686868' }} className='fs-5 fw-light pt-3'>{StrategyDetails}</p>
            </div>
          </div>
        </div>
      </div>
      <div className='container-fluid' style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
        <div className="row p-0 my-2 mx-auto justify-content-center align-items-baseline  py-5 ">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center' }}>{MISSION}</h3>
          </div>
          <div className="col-md-9 rounded" >
            <div className='mx-auto' style={{ maxWidth: '1000px', textAlign: 'center' }}>
              <p style={{ color: '#686868' }} className='fs-5 fw-light pt-3'>{MISSIONDetails}</p>
            </div>
          </div>
        </div>
      </div>
      <div className='container-fluid'>
        <div className="row p-0 my-2 mx-auto justify-content-center align-items-baseline  py-5 ">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center' }}>{OURGOAL}</h3>
          </div>
          <div className="col-md-9 rounded " >
            <div className='mx-auto' style={{ maxWidth: '1000px', textAlign: 'center' }}>
              <p style={{ color: '#686868' }} className='fs-5 fw-light pt-3'>In a low gold price environment, we will achieve this by creating value by minimizing risks related to the exploration, mining, and processing of our mineral resources and increasing efficiency.</p>
              <p style={{ color: '#686868' }} className='fs-5 fw-light pt-3'>Our primary focus is on gold production in the Arab Republic of Egypt, but we may diversify into other minerals and countries over time.</p>
            </div>
          </div>
        </div>
      </div>
      <div className='container-fluid' style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
        <div className="row p-0  mx-auto justify-content-center align-items-baseline  py-5">
          <div className="col-md-3">
            <h3 style={{ color: '#E66626', textAlign: 'center' }}>{POLICY}</h3>
          </div>
          <div className="col-md-9 rounded" >
            <div className='mx-auto' style={{ maxWidth: '1000px', textAlign: 'center' }}>
              <p style={{ color: '#686868' }} className='fw-light fs-5 pt-3'>{POLICYDetails[0]}</p>
              <p style={{ color: '#686868' }} className='fw-light fs-5 pt-3'>{POLICYDetails[1]}</p>
              <p style={{ color: '#686868' }} className='fw-light fs-5 pt-3'>{POLICYDetails[2]}</p>
            </div>
          </div>
        </div>
      </div>
      </>
  }
  </>
}
