import { createContext, useContext, useEffect, useState } from "react";
import { baseUrl } from "../../BaseUrl/baseUrl";
import axios from "axios";
import { homePageContext } from "./homePageContext";
export let newsPageContext = createContext(0)
export function NewsPageContextProvider(props) {
    const [newsData, setnewsData] = useState()
    useEffect(() => {
        function getImgs() {
            setTimeout(() => {
                axios.get(baseUrl + `newspage`)
                    .then(res => {
                        setnewsData(res?.data?.allData);
                    }).catch(err => console.log(err))
            }, 500);
        }
        getImgs()
    }, [])
    return <newsPageContext.Provider value={{ newsData }}>{props.children}</newsPageContext.Provider>
}