import React, { useContext } from 'react'
import { homePageContext } from '../Context/homePageContext.js'


export default function InvalidRouting() {
    const {x} = useContext(homePageContext)
    return <>
        <div className="container" style={{ marginTop: '120px' }}>
            <div className="row justify-content-center align-items-center">
                <div className="col-md-6">
                    <img src={x[3]?.secure_url} className='w-100' alt="" />
                </div>
                <div className="col-md-6">
                    <h1 className='text-center main-color'><span style={{color:'#00cc66'}}>404 page</span> You have inserted an invalid routing make sure of what you are looking for</h1>
                </div>
            </div>
        </div>

    </>
}
