import React, { useContext, useEffect, useState } from 'react'
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
// import Carousell from '../../Slider/Carousell';
import axios from 'axios';
import { baseUrl } from '../../../BaseUrl/baseUrl';
import { NavLink } from 'react-router-dom';
export default function Relations(props) {
    const relations = props.consultant.split(',');
    const [RelationsData, setRelationsData] = useState()
    useEffect(() => {
        function getImgs() {
            setTimeout(() => {
                axios.get(baseUrl + `relations`)
                    .then(res => {
                        setRelationsData(res.data.allData);
                    }).catch(err => console.log(err))
            }, 500);
        }
        getImgs()
    }, [])

    return <>
        <section className='text-center my-5 paragraphcolor py-5'>
            <h2 className='text-center main-color mb-5'>{relations[1]}</h2>
            <div className="container">
                <div className="row justify-content-center text-center">
                    {RelationsData?.map((ele) =>
                        <div className='col-md-4 my-2'><NavLink target='_blank' style={{ textDecoration: 'none', cursor: 'pointer' }} className='main-color' to={ele?.url}>
                                <img className='w-50' style={{ height: '100px' }} src={ele?.image?.secure_url} alt={`${ele?.title}`} />
                            <p className='my-3'>{ele?.title}</p>
                        </NavLink></div>)}
                </div>
            </div>
        </section>
    </>
}

