import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { homePageContext } from "../Context/homePageContext";
import axios from "axios";
import { baseUrl } from "../../BaseUrl/baseUrl";
// import Slider from "../Carousel/Carousel";
export default function Career() {
  let career;
  // let imgs = [{ image: img }, { image: img1 }, { image: img2 }, { image: img3 }]
  const { x } = useContext(homePageContext)
  if (x !== undefined && x !== null) {
    career = x[24]?.secure_url;
  }
  const [job, setJob] = useState()
  const [mainData, setmainData] = useState()
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `career`)
          .then(res => {
            setJob(res?.data?.allData);
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])

  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `career/maindata`)
          .then(res => {
            setmainData(res?.data?.allData[0]);
            console.log(mainData);
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  return <>{job && mainData && career &&
    <>
      <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-inner position-relative " style={{ height: "63vh" }} >
          <div className="carousel-item active">
            <div className="img career" style={{ backgroundImage: `url(${career})` }}><div style={{ backgroundColor: 'rgba(0,0,0,0.1)', width: '100%', height: '100%' }}></div></div>
          </div>
          <div className="d-flex justify-content-center align-items-center">
            <h3 className={`text-white position-absolute text fw-light `} style={{ paddingTop: "30%" }} > </h3>
          </div>
        </div>
      </div>
      <div className={`containerfluid container-fluid p-0 overflow-hidden my-5`}>
        <div className="row m-0">
          <h3 style={{ color: '#686868', position: 'relative' }}>CAREER</h3>
        </div>
        <div className="row mt-4 d-flex justify-content-center align-items-center text-center py-4 paragraphcolor">
          <h6 className="text-center maincolor my-4 fs-5">We appreciate your joining and wish that you would be one of us.</h6>
        </div>
      </div>
      <div className="container my-5">
        <div className="row justify-content-center d-flex justify-content-between mb-3">
          {job?.map((ele, i) => {
            return <div key={i} className="col-md-3 border rounded py-4 px-5 text-center">
              <div className="d-flex">
                <div className="col-md-2">
                  <i className="fa-solid maincolor fa-suitcase fs-2"></i>
                </div>
                <div className="col-md-10">
                  <h6 className="main-color fs-5">{ele?.jobTitle}</h6>
                </div>
              </div>
              <div className="col-md-12 my-3">
                <p>Experience: <span>{ele?.experience}</span></p>
              </div>
              <Link to="/jobapply" className="btn mainbtn border py-2 px-3 text-center">{mainData?.apply}</Link>
            </div>
          })}
        </div>
      </div>
    </>
  }</>
}
