import React, { useContext, useEffect, useState } from 'react'
import { baseUrl } from '../../../BaseUrl/baseUrl';
import axios from 'axios';
import { aboutUsPageContext } from '../../Context/aboutUsPageDataContext';
export default function Management() {
  const [img, setimg] = useState()
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl + `aboutpage/aboutuspageimg`)
          .then(data => {
            if (data !== null || data !== undefined) {
              console.log(data);
              setimg(data)
            }
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  let image = img?.data?.allPhotos[0]?.images[1]?.secure_url;
  console.log(image);
  let { aboutUs } = useContext(aboutUsPageContext)
  var ChairmanStatement, ChairmanStatementDetails;
  if (aboutUs !== null && aboutUs !== undefined) {
    var { ChairmanStatement, ChairmanStatementDetails } = aboutUs;
  }
  ChairmanStatement = ChairmanStatement?.split(" ")
  ChairmanStatementDetails = ChairmanStatementDetails?.split("...")

  return <>
    {ChairmanStatement && ChairmanStatementDetails && <div className="container my-5">
      <div className="row">
        <div className="col-md-4">
          <img className='w-100 my-5 img-thumbnail' src={image} alt="" />
        </div>
        <div className="col-md-8">
          <h4 className=' text-center' style={{ color: '#686868' }}><span style={{ color: '#fd7e14' }}>{ChairmanStatement[0]}</span> {ChairmanStatement[1]}</h4>
          <div className="row justify-content-start">
            <p className='fw-normal'>{ChairmanStatementDetails[0]}</p>


            <p className='fw-normal'>{ChairmanStatementDetails[1]}</p>

            <p className='fw-normal'>{ChairmanStatementDetails[2]}</p>

            <p className='fw-normal'>{ChairmanStatementDetails[3]}</p>

            <p className='fw-normal'>{ChairmanStatementDetails[4]}</p>
            <h4>Chairman Signature </h4>
            <span className='fw-bold fs-3' style={{ color: '#E66626', fontFamily: 'janetanice-font' }} > ISSAM KHORANI </span>
          </div>
        </div>
      </div>
    </div >
    }
  </>
}


