import React, { useContext } from 'react'
import './News.css'
import { NavLink } from 'react-router-dom'
import { newsPageContext } from '../Context/newsPageContext';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
export default function News(props) {
  const { newsData } = useContext(newsPageContext)
  // const { newsData } = useContext(homePageContext)

  let newsDetails;
  if (props !== null || props !== undefined) {
    newsDetails = props?.newsDetails?.split(',');
  }
  const options = {
    items: 3,
    loop: true,
    autoplay: true,
    autoplayTimeout: 4000,
    animateout: 'slideoutup',
    dots: false,
    center: true,
    responsive: {
      1100: { items: 3, innerwidth: "100%", outerwidth: "100%" },
      724: { items: 1 },
      500: { items: 1 },
      370: { items: 1, innerwidth: "100%", outerwidth: "100%" }
    },
    startPosition: '50px'
  }
  return <>  <div className="container-fluid p-0 my-5">
    <div style={{ width: '100%', padding: 0 }}>
      <div className="container p-5">
        <div className="row justify-content-around z-3">
          <h2 className='text-center' style={{ color: '#E66626' }}>{props?.newsText}</h2>
          <div className="container" style={{ marginLeft: '0' }}>
            <div className="row d-flex justify-content-center text-center" style={{margin:'auto'}}>
              {newsData ? <OwlCarousel className='owl-theme text-center mt-5' {...options} >
                {newsData.map((item, i) => {
                  return <div key={i} className="col-md-12 mt-3">
                    <div className="about-Img card border mx-1">
                      <div className=' p-3'>
                        <img className="card-img-top" style={{ height: '250px', justifySelf: 'center'  }} src={item?.images[0]?.secure_url} alt="News Image" />
                      </div>
                      <div className="card-body text-center" >
                        <p style={{ height: '130px' }} className="card-title fw-normal secondary-color fs-5">{item?.title}</p>
                        <hr />
                        <NavLink to={`/news/${item?._id}`} className="btn text-center mainbtn">Read More</NavLink>
                      </div>
                    </div>
                  </div>
                })}
              </OwlCarousel > : ""
              }
            </div>
          </div>


        </div></div></div></div>:''
  </>
}



