import React, { useContext, useEffect, useState } from 'react'
import { projectPageContext } from '../Context/projectPageContext'
import { NavLink, Outlet } from 'react-router-dom'
import Slider from '../Carousel/Carousel'
export default function UmRuus() {
  let { Data, imgs } = useContext(projectPageContext)
  let images,photos,pics
  if (imgs !== undefined && imgs !== null) {
     images = [{ image:imgs[7]?.secure_url }, { image: imgs[8]?.secure_url}];
     photos = [{ image: imgs[4]?.secure_url }, { image: imgs[5]?.secure_url }, { image: imgs[6]?.secure_url}]
     pics = [{ image: imgs[1]?.secure_url}, { image: imgs[2]?.secure_url }, { image: imgs[3]?.secure_url }]
  }
  return <>
    {Data && imgs && <>
      <div className="container my-5" >
        <div className="row justify-content-center text-center">
          <div className="col-md-10">
            <p style={{ color: '#686868' }} className='fs-5'><span className='fs-4' style={{ color: '#E66626', fontFamily: 'Copperplate Gothic Bold' }}>{Data?.NubianMiningCompany}</span>{Data?.umRuusIntro}</p>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="row ">
          <h5 className='main-color' style={{ position: 'relative' }}>{Data?.ProjectUmRuusOverview}</h5>
          <div className="col-lg-8">
            <p className='fs-5 mb-5 mt-3 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmRuusOverviewDetails?.split("...")[0]}</p>
            <p className='fs-5 mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmRuusOverviewDetails?.split("...")[1]}</p>
          </div>
          <div className="col-lg-4">
            <img src={imgs[0]?.secure_url} className='w-100' alt="" />
          </div>
          <p className='fs-5 mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmRuusOverviewDetails?.split("...")[2]}</p>

        </div>
        <div className="row my-5">
          <h5 className='main-color'>{Data?.Zoningtheconcession}</h5>
          <h6 className='main-color fs-5'>{Data?.ZoningTheConcessionIntro}</h6>
          <div className="col-md-12 secondary-color">
            <ol>
              <li className='fs-5'>
                <p>{Data?.ZoningTheConcessionDetails?.split("...")[0]}</p>
              </li>
              <li className='fs-5'>
                <p>{Data?.ZoningTheConcessionDetails?.split("...")[1]}</p>
              </li>
              <li className='fs-5'>
                <p>{Data?.ZoningTheConcessionDetails?.split("...")[2]}</p>
              </li>
              <li className='fs-5'>
                <p>{Data?.ZoningTheConcessionDetails?.split("...")[3]}</p>
              </li>
            </ol>
          </div>
        </div>
        <div className="row">
          <h5 className='main-color'>{Data?.InfrastructureandConstructions}</h5>
          <div className="col-md-6 secondary-color">
            <p className='fs-5'>{Data?.InfrastructureandConstructionsdetails}</p>
          </div>
          <div className="col-md-6">
            <Slider imgs={pics} height={'500px'} />
          </div>
        </div>
        <div className="row my-5">
          <h5 className='main-color'>{Data?.CoreYard}</h5>
          <h6 className='main-color fs-5'>{Data?.coreYardSteps}</h6>
          <div className="col-md-12">
            <ol className='fs-5 secondary-color'>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[0]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[1]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[2]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[3]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[4]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[5]}</p>
              </li>
              <li>
                <p>{Data?.CoreYardDetails?.split("...")[6]}</p>
              </li>
            </ol>
          </div>
          <div className="col-md-12">
            <Slider imgs={photos} />
          </div>
        </div>
        <div className="row justify-content-center text-center my-5">
          <h5 className='main-color'>{Data?.ExpenditureUmRuus}</h5>
          <div className="col-md-12">
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ExpenditureDetailsUmRuus}</p>
          </div>
        </div>
        <div className="row">
          <h5 className='main-color'>{Data?.GoldMineralizationStyles}</h5>
          <div className="col-md-6">
            <p className='mb-5 fs-5 fw-normal secondary-color'>{Data?.GoldMineralizationStylesDetails}</p>
          </div>
          <div className="col-md-6">
            <Slider imgs={images} height={'500px'} />
          </div>
        </div>
        <div className="row mt-5">
          <h5 className='main-color'>{Data?.PhasesOfExplorationAtTheConcessionArea}</h5>
          <div className="col-md-12">
            <p className='fs-5 fw-normal secondary-color'>{Data?.PhasesOfExplorationAtTheConcessionAreaDetails?.split("...")[0]}</p>
            <p className='fs-5 fw-normal secondary-color'>{Data?.PhasesOfExplorationAtTheConcessionAreaDetails?.split("...")[1]}</p>

          </div>
        </div>
      </div>
      <div className="container-fluid overflow-hidden p-0 my-5">
        <div className="row mt-4 d-flex justify-content-center align-items-center text-center py-4" style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
          <div className="col-md-2"><NavLink style={{ cursor: 'pointer', textDecoration: 'none', textDecorationStyle: 'none', color: '#E66626' }} className='fs-5' to='/Our-Projects/umruus/mapping'><h6>{Data?.MappingAndSampling}</h6></NavLink></div>
          <div className="col-md-2"> <NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} className='fs-5' to='/Our-Projects/umruus/trench'><h6>{Data?.Trench}</h6></NavLink></div>
          <div className="col-md-2"> <NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} className='fs-5' to='/Our-Projects/umruus/rc'><h6>{Data?.RCDrilling}</h6></NavLink></div>
          <div className="col-md-2"> <NavLink style={{ cursor: 'pointer', textDecoration: 'none', color: '#E66626', textDecorationStyle: 'none' }} className='fs-5' to='/Our-Projects/umruus/dd'><h6>{Data?.DiamondDrilling}</h6></NavLink></div>
        </div>
        <Outlet />
      </div>


    </>
    }

  </>
}





























