import React, { useContext } from 'react'
import { projectPageContext } from '../Context/projectPageContext'
export default function UmRuus() {
  let { Data, imgs } = useContext(projectPageContext)
  console.log(Data);
  return <>
    {imgs && Data && <><div className="container my-5" >
      <div className="row justify-content-center text-center">
        <div className="col-md-10">
          <p style={{ color: '#686868' }} className='fs-5'><span className='fs-4 main-color' style={{ fontFamily: 'Copperplate Gothic Bold' }}>{Data?.NubianMiningCompany}</span> entered into a Joint Venture (JV) with Shalateen Mineral Resources Company (SMRC) to explore part of the Aswan Concession held by SMRC. The terms of the JV are contained in an Agreement dated 14 Oct 2019</p>
        </div>
      </div>
    </div>
      <div className="container">
        <div className="row p-3">
          <h5 className='mb-3 main-color' style={{ position: 'relative' }}>{Data?.ProjectUmShakiebOverview}</h5>
          <div className="col-md-8">
            <p className='mb-5 mt-3 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmShakiebOverviewDetails.split('...')[0]}</p>
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmShakiebOverviewDetails.split('...')[1]}</p>
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmShakiebOverviewDetails.split('...')[2]}</p>
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmShakiebOverviewDetails.split('...')[3]}</p>
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ProjectUmShakiebOverviewDetails.split('...')[4]}</p>
          </div>
          <div className="col-md-4">
            <img style={{ width: '100%' }} src={imgs[20]?.secure_url} alt="" />
          </div>
        </div>
      </div>

      <div className="container-fluid">
        <div className="row p-3 justify-content-center">
          <h5 className='mb-3 main-color' style={{ position: 'relative' }}>{Data?.geologyumshakieb}</h5>
          <div className="col-md-8">
            <p className='mb-5 mt-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.geologyumshakiebdetails?.split('...')[0]}</p>
            <p className='mb-5 mt-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.geologyumshakiebdetails?.split('...')[1]}</p>
            <p className='mb-5  mt-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.geologyumshakiebdetails?.split('...')[2]}</p>
            <p className='mb-5  mt-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.geologyumshakiebdetails?.split('...')[3]}</p>
          </div>
          <div className="col-md-4 gy-5">
            <img style={{ width: '80%' }} src={imgs[21]?.secure_url} alt="" />
            <img style={{ width: '80%' }} src={imgs[22]?.secure_url} alt="" />
          </div>
        </div>
        <div className="row justify-content-center text-center my-5">
          <h5 className='main-color'>{Data?.ExpenditureUmRuus}</h5>
          <div className="col-md-12">
            <p className='mb-5 fs-5 fw-normal' style={{ color: '#686868' }}>{Data?.ExpenditureDetailsUmSahkieb}</p>
          </div>
        </div>
      </div>

      <div className="container-fluid">
        <div className="row p-3 justify-content-center align-items-center d-flex text-center">
          <h5 className='mb-3 ' style={{ color: '#ffbc13', position: 'relative' }}>Technical Support</h5>
          <div className="col-md-12">
            <p className='mb-5 mt-3 fs-5 fw-normal' style={{ color: '#686868' }}>NMC entered into a contract with SRK ES and ANG to provide technical support. SRK Exploration Services Limited (SRK ES) is an associated company of the international group Holding company, SRK Consulting (Global) Limited (theSRK Group)</p>
            <p className='mb-5 mt-3 fs-5 fw-normal' style={{ color: '#686868' }}>Arab Nubia Group (ANG) which is a company specialized in the mining services and has various services among geology investigations and technical supporting in the mining industry</p>

          </div>
        </div>
      </div>

    </>}
  </>
}


