import { createContext, useEffect, useState } from "react";
import { baseUrl } from "../../BaseUrl/baseUrl";
import axios from "axios";
export let sustainabilityPageContext = createContext(0)
export function SustainabilityPageContextProvider(props) {
  const [data, setData] = useState()
  const [imgs, setImgs] = useState()
  useEffect(() => {
    function getData() {
      setTimeout(() => {
        axios.get(baseUrl +`sustainabilitypage/sustainabilitypagedata`)
          .then(data => {
            setData(data?.data?.allData[0]);
          }).catch(err => console.log(err))
      }, 500);
    }
    getData()
  }, [])
  useEffect(() => {
    function getImgs() {
      setTimeout(() => {
        axios.get(baseUrl +'sustainabilitypage/sustainabilitypageimg')
          .then(x => {
            setImgs(x?.data?.allPhotos[0]?.images);
          }).catch(err => console.log(err))
      }, 500);
    }
    getImgs()
  }, [])
  return <sustainabilityPageContext.Provider value={{ data, setData, imgs, setImgs }}>{props.children}</sustainabilityPageContext.Provider>
}