import React from "react";

export default function JobApply() {
  return <>
  <div className="container my-5">
    <div className="row">
    <form className="form-control p-5" action={`https://getform.io/f/072c1061-7e35-4249-ad03-b1097cb23a7d`} enctype="multipart/form-data" target="_blank" method="POST">
      <div className="container text-center">
        <div className="row justify-content-center gy-4">
          <div className="col-md-6">
            <input className="form-control" name="name" placeholder="Name" type="text" />
            {/* {formik.errors.name && formik.touched.name ? <div className='alert alert-warning'>{formik.errors.name}</div> : ''} */}
          </div>
          <div className="col-md-6">
            <input className="form-control" name="email" placeholder="E-mail" type="email" />
            {/* {formik.errors.email && formik.touched.email ? <div className='alert alert-warning'>{formik.errors.email}</div> : ''} */}
          </div>
          <div className="col-md-6">
            <input className="form-control" name="phone" placeholder="Phone Number" type="phone" />
            {/* {formik.errors.phone && formik.touched.phone ? <div className='alert alert-warning'>{formik.errors.phone}</div> : ''} */}
          </div>
          <div className="col-md-6">
            <input className="form-control" name="subject" placeholder="Subject" type="text" />
            {/* {formik.errors.subject && formik.touched.subject ? <div className='alert alert-warning'>{formik.errors.subject}</div> : ''} */}
          </div>
          <div className="col-md-12">
            <input type="file" name="document" required="required" />
          </div>
          <div className="col-md-4">
            <button className="btn mainbtn">Send Message</button>
          </div>
        </div>
      </div>
    </form>
    </div>
  </div>
  </>
}
