import React, { useContext } from 'react'
import { projectPageContext } from '../Context/projectPageContext'
import Slider from '../Carousel/Carousel';
export default function Mapping() {
  let { Data, imgs } = useContext(projectPageContext)
  let samplePhoto;
  if (imgs !== null && imgs !== undefined) {
    samplePhoto = [{ image: imgs[9]?.secure_url }, { image: imgs[10]?.secure_url }, { image: imgs[11]?.secure_url }]

  }
  return <>
    <div className="container my-5">
      <div className="row justify-content-center text-center">
        <h5 className='main-color text-start'>{Data?.MappingAndSampling}</h5>
        <div className="col-md-12 ">
          <p className='text-start mt-3 secondary-color fs-5'>{Data?.MappingAndSamplingDetails}</p>
        </div>
        <div className="col-md-12">
          <Slider imgs={samplePhoto} width={'20%'} />
        </div>
      </div>
    </div>
  </>
}
