import React, { useContext } from 'react'
import { sustainabilityPageContext } from '../../Context/sustainabilityPageContext'
// import img from '../../../images/people/20221220_174046--.jpg'
// import img1 from '../../../images/people/IMG-20220303-WA0014.jpg'
// import img2 from '../../../images/people/IMG-20220303-WA0014.jpg'
// import img3 from '../../../images/people/IMG-20220303-WA0014.jpg'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick'
export default function OurPeople() {
  let { data } = useContext(sustainabilityPageContext)
  var settings = {
    dots: true,
    infinite: true,
    speed: 100,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  // let x = [img, img1, img2, img3]
  return <>
    <div className="container-fluid pt-5 text-center" >
      <div className="row justify-content-center align-items-center">
        <div className="col-md-12">
          <h5 className='mb-4 maincolor text-start' >{data?.ourpeople}</h5>
          <div>
            <h6 className='fs-4 fw-normal' style={{ color: '#686868' }}>{data?.ourpeopleDetails.split('...')[0]}</h6>
            <h6 className='fs-4 fw-normal my-3' style={{ color: '#686868' }}>{data?.ourpeopleDetails.split('...')[1]}</h6>
            <ul className='text-start' style={{ color: '#727272' }}>
              <li className='fs-5 fw-normal'>{data?.ourpeopleDetails.split('...')[2]}</li>
              <li className='fs-5 fw-normal'>{data?.ourpeopleDetails.split('...')[3]}</li>
              <li className='fs-5 fw-normal'>{data?.ourpeopleDetails.split('...')[4]}</li>
              <li className='fs-5 fw-normal'>{data?.ourpeopleDetails.split('...')[5]}</li>
              <li className='fs-5 fw-normal'>{data?.ourpeopleDetails.split('...')[6]}</li>
            </ul>
          </div>
        </div>
        <div className="col-md-12 text-center">
          {/* <i className="fa-regular fa-handshake text-center" style={{ fontSize: '5rem', color: '#E66626' }} ></i> */}

        </div>
      </div>
    </div>
    <div className="container-fluid py-3" style={{ backgroundColor: 'rgba(104,104,104,0.1)' }}>
      <div className="container-fluid py-3">
        <div className="row justify-content-center">
          <div className="col-md-12">
            <h5 style={{ color: '#E66626' }}>{data?.diversityAndInclusion}</h5>
            <div>
              <p className=' fs-5 fw-light' style={{ color: '#686868' }}>{data?.diversityAndInclusionDetails?.split("...")[0]}</p>
              <p className=' fs-5 fw-light my-3' style={{ color: '#686868' }}>{data?.diversityAndInclusionDetails?.split("...")[1]}</p>
              <p className=' fs-5 fw-light my-3' style={{ color: '#686868' }}>{data?.diversityAndInclusionDetails?.split("...")[2]}</p>
            </div>
          </div>
          {/* <div className="col-md-12 text-center">
            <Slider  {...settings}>
              {x.map((image) => (
                <img className='w-50' src={image} alt="" />
              ))}
</Slider>
          </div> */}
        </div>
      </div>
    </div>
  </>
}



